import * as React from 'react';
import {NavBarTop, NavBarTopVariant} from '@here/hds-navbar';

// eslint-disable-next-line no-new
new NavBarTop();

interface HDSNavBarTopProperties extends React.HTMLAttributes<HTMLElement> {
  variant?: NavBarTopVariant;
}

const HDSNavBarTop = React.forwardRef((props: HDSNavBarTopProperties, ref) => {
  const {children, className, variant, ...rest} = props;
  return (
    <hds-navbar-top
      ref={ref}
      {...(variant && {variant})}
      {...(className && {class: className})}
      {...rest}
    >
      {children}
    </hds-navbar-top>
  );
});

HDSNavBarTop.defaultProps = {
  variant: 'default',
};

export default HDSNavBarTop;
